import {
    ChonkyActions,
    FileBrowser,
    FileContextMenu,
    FileHelper,
    FileList,
    FileNavbar,
    FileToolbar,
    setChonkyDefaults,
} from 'chonky';
import { ChonkyIconFA } from 'chonky-icon-fontawesome';
import PropTypes from 'prop-types';
import React from 'react';
import { useCallback, useMemo, useState} from 'react';

setChonkyDefaults({
  iconComponent: ChonkyIconFA,
  disableDragAndDrop: true
});

export default function FilePicker(props) {
  const {
    id,
    setProps,
    ...otherProps
  } = props;

  const useFiles = (currentFolderId) => {
      return useMemo(() => {
          const currentFolder = fileMap[currentFolderId];
          const files = currentFolder.childrenIds
              ? currentFolder.childrenIds.map((fileId) => fileMap[fileId] ?? null)
              : [];
          return files;
      }, [currentFolderId]);
  };

  const useFolderChain = (currentFolderId) => {
      return useMemo(() => {
          const currentFolder = fileMap[currentFolderId];

          const folderChain = [currentFolder];

          let parentId = currentFolder.parentId;
          while (parentId) {
            const parentFile = fileMap[parentId];
            if (parentFile) {
              folderChain.unshift(parentFile);
              parentId = parentFile.parentId;
            } else {
              parentId = null;
            }
          }

          return folderChain;
      }, [currentFolderId]);
  };

  const useFileActionHandler = (setCurrentFolderId) => {
    return useCallback(
        (data) => {
          if (data.id === ChonkyActions.OpenFiles.id) {
            // if open action occurred on a folder, open it
            const { targetFile, files } = data.payload;
            const fileToOpen = targetFile ?? files[0];
            if (fileToOpen && FileHelper.isDirectory(fileToOpen)) {
                setCurrentFolderId(fileToOpen.id);
                return;
            }
          }
          else if (data.id === ChonkyActions.ChangeSelection.id) {
            // get selected files
            let selectedFiles = data.payload.selection;
            selectedFiles = Array.from(selectedFiles);
            setProps({ "selectedFiles" : selectedFiles })
            //console.log("selected:", selectedFiles);
            return;
          }
        },
        [setCurrentFolderId]
    );
  };

  const fileMap = props.fileMap.fileMap || {};
  const rootFolderId = props.fileMap.rootFolderId;

  const [currentFolderId, setCurrentFolderId] = useState(rootFolderId);
  const files = useFiles(currentFolderId);
  const folderChain = useFolderChain(currentFolderId);
  const handleFileAction = useFileActionHandler(setCurrentFolderId);

  const actionsToDisable = [ ChonkyActions.OpenSelection.id ];

  return (
      <FileBrowser
        files={files}
        folderChain={folderChain}
        onFileAction={handleFileAction}
        defaultFileViewActionId={ChonkyActions.EnableListView.id}
        disableDefaultFileActions={actionsToDisable}>
        <FileNavbar />
        <FileToolbar />
        <FileList />
        <FileContextMenu />
      </FileBrowser>
  );
};

FilePicker.defaultProps = {
    selectedFiles : []
};

FilePicker.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,

    /**
     * Selected files.
     */
    selectedFiles: PropTypes.array,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func
};